@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Manrope', sans-serif;
}

.container {
  overflow: hidden;
  position: relative;
  height: 100vh;
  background: #0e0e0e;
}

.main-content {
  color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 110px 100px;
  display: flex;
  justify-content: space-between;
}

.left-ctn {
  .texted {
    line-height: 1.1;
    .intro {
      font-weight: 800;
      color: rgba(221, 221, 221, 0.6);
      font-size: calc(50px + 2vmin);
      bold {
        color: #fff;
      }
    }
  }
  .middles {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 10vh 0px;
    p {
      margin: 10px 0px;
      width: 50%;
    }
    .get {
      margin: 40px 0px;
      color: rgba(221, 221, 221, 0.6);
    }
  }
  .icon-g {
    display: flex;
    align-items: center;
    gap: 40px;
    i {
      font-size: calc(12px + 2vmin);
    }
  }
}

.right-ctn {
  width: 50%;
  .skil {
    width: 50%;
    margin: 20px 0px;
    color: rgba(221, 221, 221, 0.6);
  }
  .skil1 {
    width: 100%;
    margin: 20px 0px;
    color: rgba(221, 221, 221, 0.6);
  }
  .texted-flex {
    display: flex;
    margin: 50px 0px;
    align-items: center;
  }
}
